import React from 'react';

import s from './Tcontact';

function Tcontact() {
    return (
        <div className={s.Tcontact}>
            <div className={s.title}>联系我们</div>
            <div className={s.contact}>AIP@moji.com</div>
        </div>
    );
}

export default Tcontact;



